.opis {
    font-weight: bold;
}

.viewPogodaMiejscowa > div{
    padding: 20px 0px;
    align-items: center;
    align-content: center;
    background-color: white;
}

.viewPogodaImages{
    width: 40px;
    height: 40px;
    margin: 0;
    padding: 0;
}
.viewPogoda__div {
    padding-left: 10px;
}

.ViewPogodaImagesDiv {
  display: flex;
  border-top: 1px solid black;

}   
.viewPogodaMiejscowa {
    display: grid;
    margin: 0px;
    padding: 0px;
    grid-template-columns: 1fr 60px 40px 60px 40px;
    grid-template-rows: repeat(5,60px);
    border: 1px solid black;
    background-color: rgb(166, 194, 226)
}


@media(min-width: 500px) {
    .viewPogodaMiejscowa {
        display:grid;
        grid-template-columns: 1fr 100px 100px 100px 100px;
    }
}

@media(min-width:1000px){
    .viewPogodaMiejscowa {
        grid-template-columns: 1fr repeat(4, 140px);
        grid-template-rows: repeat(5,80px);
        font-size: 20px;
    }
    .viewPogoda__div {
        text-align: center;
        /* style={{paddingLeft: '10px', fontWeight:'700'}}  */
    }

    .ViewPogodaImagesDiv img {
        width: 60px;
        height: 60px;
    }
}
